a,
div,
img,
p,
button {
  -ms-user-select: none;
      user-select: none;
  -webkit-user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
}

.ImageUploader input {
    display: none;
}

.ImageUploader__label {
    background-color: var(--input_background);
    border: 1px solid var(--input_border);
    width: 72px;
    height: 72px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    background-size: cover;
    background-position: center;
}
.AudioUploader__audioLine {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.AudioUploader__audioIcon {
    background-color: var(--input_background);
    width: 48px;
    min-width: 48px;
    height: 48px;
    min-height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    margin-right: 12px;
}

.AudioUploader__audioBox {
    display: flex;
    align-items: center;
}

.AudioUploader__audioDuration {
    color: var(--text_tertiary);
    font-size: 13px;
    line-height: 16px;
    letter-spacing: 0.2px;
    margin-left: 10px;
}

.AudioUploader__placeholder .Placeholder__in {
    padding-top: 20px;
    padding-bottom: 20px;
}

.AudioUploader__EditInfo p {
    font-size: 14px;
    line-height: 16px;
    color: var(--content_placeholder_text);
    letter-spacing: -0.078px;
    margin-top: 10px;
    margin-bottom: 18px;
}

.AudioUploader__EditInfo .Button {
    margin-left: 0!important;
    margin-right: 0!important;
    width: 100%!important;
}

.Preview__snippet {
    display: flex;
    align-items: center;
    margin-top: 3px!important;
}

.Preview__snippet__cover {
    width: 72px;
    min-width: 72px;
    height: 72px;
    min-height: 72px;
    background-size: cover;
    background-position: center;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin-right: 14px;
}

.Preview__snippet__cover__icon {
    width: 32px;
    height: 32px;
    background-color: rgba(0, 0, 0, 0.35);
    border-radius: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Preview__snippet__info__title {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 6px;
}

.Preview__snippet__info__group {
    margin-bottom: 6px;
    line-height: 14px;
    font-size: 12px;
}

.Preview__snippet__info__group a {
    color: var(--link_alternate);
    text-decoration: none;
}

.Preview__snippet__info__group a:hover {
    text-decoration: underline;
}

.Preview__snippet__info__duration {
    font-size: 12px;
    line-height: 14px;
    color: var(--header_text_secondary);
}

.Preview__sectionTitle {
    margin-bottom: 14px;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
}

.Preview__description p {
    font-size: 15px;
    line-height: 20px;
}


.TimecodeLine {
    display: flex;
    align-items: center;
    margin: 12px 0;
}

.timecodeDescrition {
    width: 60%;
    margin-right: 16px;
}

.timecodeTime {
    width: 30%;
    margin-right: 16px;
}

.Preview__timecodeLine {
    font-size: 16px;
    line-height: 20px;
    padding: 14px 0;
}

.Preview__timecodeLine a {
    text-decoration: none;
    color: var(--text_link)
}

#timeline {
    background-color: var(--field_background);
    height: 26px;
    display: flex;
    align-items: flex-end;
    border-top-right-radius: 8px;
    border-top-left-radius: 8px;
    border: 0.5px solid var(--field_border);
}

#waveform {
    background-color: var(--field_background);
    border-left: 0.5px solid var(--field_border);
    border-right: 0.5px solid var(--field_border);
}

.AudioEditor__controls {
    display: flex;
    justify-content: space-between;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
    border-bottom: 0.5px solid var(--field_border);
    border-left: 0.5px solid var(--field_border);
    border-right: 0.5px solid var(--field_border);
    padding: 8px 6px;
}

.AudioEditor__controls__group {
    display: flex;
}

.AudioEditor__controls__play {
    background-color: var(--accent);
    width: 44px;
    height: 44px;
    min-height: 44px;
    min-width: 44px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2px;
}

.AudioEditor__controls__secondary--inactive {
    background-color: var(--button_muted_background);
    opacity: 40%;
}

.AudioEditor__controls__secondary {
    background-color: var(--button_muted_background);
    width: 44px;
    height: 44px;
    min-height: 44px;
    min-width: 44px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2px;
}

.AudioEditor__controls__secondary--active {
    background-color: var(--accent);
}

.AudioEditor__controls__secondary .Icon {
    color: #3F8AE0;
}

.AudioEditor__controls__secondary--active svg path {
    stroke: #ffffff;
}

.AudioEditor__controls__secondary--active .Icon {
    color: #ffffff;
}

.AudioEditor__controls__secondary img {
    color: #3F8AE0;
}

.wavesurfer-handle {
    width: 6px!important;
    background-color: var(--accent)!important;
    max-width: 8px!important;
}

.wavesurfer-handle:after {
    content: "";
    display: block;
    width: 2px;
    height: 12px;
    background-color: #fff;
    border-radius: 2px;
    left: 2px;
    top: calc(50% - 6px);
    position: absolute;
}

.wavesurfer-region {
    background-color: transparent!important;
    border-top: 2px solid var(--accent);
    border-bottom: 2px solid var(--accent);
    height: calc(100% - 4px)!important;
}

region:after, region:before {
    position: absolute;
    display: block;
    font-size: 9px;
    line-height: 12px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    color: #99A2AD;
    text-align: center;
    letter-spacing: 0.3px;
    white-space: nowrap;
    z-index: -1;
}

region[data-id="fadeIn"]:after {
    content: "\41F\43E\44F\432\43B\435\43D\438\435: \432\43A\43B";
    position: absolute;
    display: block;
    left: 6px;
    bottom: 0;
}

region[data-id="fadeOut"]:after {
    content: "\417\430\442\443\445\430\43D\438\435: \432\43A\43B";
    position: absolute;
    display: block;
    left: 6px;
    bottom: 0;
}

